import Layout from '../components/MyLayout';
import Link from 'next/link';

const PostLink = props => (
    <li>
        <Link href={`/post?title=${props.title}`}>
            <a>{props.title}</a>
        </Link>
    </li>
);



const Index = () => (
    <Layout>
        <p>Hello Next.js</p>
    </Layout>
)

